import React, { Reducer, useContext, useEffect, useReducer, useState } from 'react';
import * as PIXI from 'pixi.js';
import { AnimationContext, PixiDisplayObject, RenderingContext, TextureContext, useElement } from 'pixi-reactive';
import { PixiSpineProps } from '../props';
import 'pixi-spine';

const PixiSpine: React.FC<PixiSpineProps> = (props) => {
  const element = useElement(new PIXI.Container());
  const { resources } = useContext(TextureContext);
  const { frameId, elapsed } = useContext(AnimationContext);
  const { update } = useContext(RenderingContext);
  const [playing, setPlaying] = useState(false);
  const [spine, setSpineElement] = useReducer<Reducer<PIXI.spine.Spine | undefined, PIXI.spine.Spine>>(
    (previousElement: PIXI.spine.Spine | undefined, newElement: PIXI.spine.Spine) => {
      if (previousElement) {
        previousElement.destroy();
      }
      return newElement;
    },
    undefined
  );
  const { resource, loop = false, animation, trackIndex = 0, skin, speed = 1 } = props;

  useEffect(() => {
    if (spine) {
      spine.skeleton.setSkin((null as unknown) as PIXI.spine.core.Skin);
      if (skin) {
        spine.skeleton.setSkinByName(skin);
      }
    }
  }, [skin, spine]);

  useEffect(() => {
    if (spine) {
      spine.setParent(element);
    }
  }, [element, spine]);

  useEffect(() => {
    if (resource && resources[resource] && resources[resource].spineData) {
      const component = new window.PIXI.spine.Spine(resources[resource].spineData);
      component.autoUpdate = false;

      setSpineElement(component);
    }
  }, [resource, resources]);

  useEffect(() => {
    if (spine) {
      if (animation && spine.state.hasAnimation(animation)) {
        spine.state.setAnimation(trackIndex, animation, loop);
        setPlaying(true);
      } else {
        spine.state.clearTrack(trackIndex);
        spine.skeleton.setToSetupPose();
        setPlaying(false);
      }
      update();
    }
  }, [spine, trackIndex, animation, loop, update]);

  useEffect(() => {
    if (spine) {
      spine.state.timeScale = speed;
    }
  }, [spine, speed]);

  useEffect(() => {
    if (playing && spine) {
      spine.update(elapsed);
    }
  }, [playing, frameId, elapsed, spine, update]);

  return <PixiDisplayObject item={element} {...props} />;
};

export default PixiSpine;
